// ボタン
// メニューバーのお問い合わせボタン
.c-menu-bar-button {
  @include media(tb) {
    background-color: $color-main;
    border-radius: 8px;
    cursor: pointer;
  }
}
// トップページのリンクボタン
.c-top-link-button {
  margin: 16px auto 0;
  border-radius: 32px;
  padding: 8px 0;
  width: auto;
  min-width: 96px;
  text-align: center;
  cursor: pointer;
  &__production-results {
    @extend .c-top-link-button;
    @include linkButton(60%);
    box-shadow: none;
  }
  &__service {
    @extend .c-top-link-button;
    @include linkButton(70%);
  }
  &__news {
    @extend .c-top-link-button;
    @include linkButton(70%);
  }
  &__contact {
    @extend .c-top-link-button;
    @include linkButton(70%);
  }
}
// 送信ボタン
.c-button {
  border: none;
  border-radius: 8px;
  padding: 8px 0;
  width: auto;
  min-width: 96px;
  text-align: center;
  cursor: pointer;
  &__submit {
    @extend .c-button;
    display: block;
    margin: 16px auto 0;
    color: $color-font-white;
    background-color: $color-main;
    box-shadow: 2px 4px 8px 1px $color-button-shadow;
  }
}
